___CSS_LOADER_EXPORT___.push([module.id, `#call-container[data-v-69c35b21]{width:100%;height:100%;background-color:rgba(34,34,34,.8);backdrop-filter:var(--filter-background-blur);--grid-gap: calc(var(--default-grid-baseline) * 2)}#call-container.call-container__blurred[data-v-69c35b21]{backdrop-filter:blur(25px)}#call-container.call-container__non-blurred[data-v-69c35b21]{backdrop-filter:none}#videos[data-v-69c35b21]{position:absolute;width:100%;height:calc(100% - 51px);top:51px;overflow:hidden;display:flex;justify-content:space-around;align-items:flex-end;flex-direction:column;padding:calc(var(--default-grid-baseline)*2);padding-block-start:0}.video__promoted[data-v-69c35b21]{position:relative;height:100%;width:100%}.video__promoted.full-page[data-v-69c35b21]{position:static}.video__promoted .dev-mode-video--promoted[data-v-69c35b21]{position:absolute;width:100%;height:100%;display:flex;justify-content:center}.video__promoted .dev-mode-video--promoted img[data-v-69c35b21]{position:absolute;height:100%;aspect-ratio:4/3;object-fit:cover;border-radius:var(--border-radius-element, calc(var(--default-clickable-area) / 2))}.local-video[data-v-69c35b21]{position:absolute;right:0;bottom:0;width:300px;height:250px}.local-video--sidebar[data-v-69c35b21]{width:150px;height:100px}#videos.hidden[data-v-69c35b21]{display:none}[data-v-69c35b21] video{z-index:0;vertical-align:top}#videos[data-v-69c35b21] video{padding:0}`, "",{"version":3,"sources":["webpack://./src/components/CallView/CallView.vue","webpack://./src/assets/variables.scss"],"names":[],"mappings":"AAGA,iCACC,UAAA,CACA,WAAA,CACA,kCCauB,CDXvB,6CAAA,CACA,kDAAA,CAEA,yDACC,0BAAA,CAED,6DACC,oBAAA,CAIF,yBACC,iBAAA,CACA,UAAA,CACA,wBAAA,CACA,QAAA,CACA,eAAA,CACA,YAAA,CACA,4BAAA,CACA,oBAAA,CACA,qBAAA,CACA,4CAAA,CACA,qBAAA,CAGD,kCACC,iBAAA,CACA,WAAA,CACA,UAAA,CAEA,4CAGC,eAAA,CAGD,4DACC,iBAAA,CACA,UAAA,CACA,WAAA,CACA,YAAA,CACA,sBAAA,CAGD,gEACC,iBAAA,CACA,WAAA,CACA,gBAAA,CACA,gBAAA,CACA,mFAAA,CAIF,8BACC,iBAAA,CACA,OAAA,CACA,QAAA,CACA,WAAA,CACA,YAAA,CAEA,uCACC,WAAA,CACA,YAAA,CAIF,gCACC,YAAA,CAGD,wBACC,SAAA,CAOA,kBAAA,CAGD,+BACC,SAAA","sourcesContent":["\n@import '../../assets/variables';\n\n#call-container {\n\twidth: 100%;\n\theight: 100%;\n\tbackground-color: $color-call-background;\n\t// Default value has changed since v29.0.4: 'blur(25px)' => 'none'\n\tbackdrop-filter: var(--filter-background-blur);\n\t--grid-gap: calc(var(--default-grid-baseline) * 2);\n\n\t&.call-container__blurred {\n\t\tbackdrop-filter: blur(25px);\n\t}\n\t&.call-container__non-blurred {\n\t\tbackdrop-filter: none;\n\t}\n}\n\n#videos {\n\tposition: absolute;\n\twidth: 100%;\n\theight: calc(100% - 51px);\n\ttop: 51px; // TopBar height\n\toverflow: hidden;\n\tdisplay: flex;\n\tjustify-content: space-around;\n\talign-items: flex-end;\n\tflex-direction: column;\n\tpadding: calc(var(--default-grid-baseline) * 2);\n\tpadding-block-start: 0;\n}\n\n.video__promoted {\n\tposition: relative;\n\theight: 100%;\n\twidth: 100%;\n\n\t&.full-page {\n\t\t// force the promoted remote or local video to cover the whole call view\n\t\t// doesn't affect screen shares, as it's a different MediaStream\n\t\tposition: static;\n\t}\n\n\t.dev-mode-video--promoted {\n\t\tposition: absolute;\n\t\twidth: 100%;\n\t\theight: 100%;\n\t\tdisplay: flex;\n\t\tjustify-content: center;\n\t}\n\n\t.dev-mode-video--promoted img {\n\t\tposition: absolute;\n\t\theight: 100%;\n\t\taspect-ratio: 4 / 3;\n\t\tobject-fit: cover;\n\t\tborder-radius: var(--border-radius-element, calc(var(--default-clickable-area) / 2));\n\t}\n}\n\n.local-video {\n\tposition: absolute;\n\tright: 0;\n\tbottom: 0;\n\twidth: 300px;\n\theight: 250px;\n\n\t&--sidebar {\n\t\twidth: 150px;\n\t\theight: 100px;\n\t}\n}\n\n#videos.hidden {\n\tdisplay: none;\n}\n\n:deep(video) {\n\tz-index: 0;\n\t/* default filter for slightly better look */\n\t/* Disabled for now as it causes a huuuuge performance drop.\n\t CPU usage is more than halved without this.\n\t -webkit-filter: contrast(1.1) saturate(1.1) sepia(.1);\n\t filter: contrast(1.1) saturate(1.1) sepia(.1);\n\t */\n\tvertical-align: top; /* fix white line below video */\n}\n\n#videos :deep(video) {\n\tpadding: 0;\n}\n\n","/**\n * SPDX-FileCopyrightText: 2019 Nextcloud GmbH and Nextcloud contributors\n * SPDX-License-Identifier: AGPL-3.0-or-later\n */\n\n/** Messages list dimensions:\n * - text max width: ~750px (80 characters per line is recommended by W3C standard)\n * - avatar width: 32px (AVATAR.SIZE.SMALL) + 16px (paddings) = 48px\n * - info width: 8ch(~68px) (timestamp) + 40px (checkmark with paddings) = ~108px\n * - list max width: 48px (avatar width) + 1058px (text width with paddings) + ~108px (info width) = ~1214px\n * - input max width: ~1214px (list max width) - 100px (send button) = ~1114px\n */\n$messages-text-max-width: calc(50 * var(--default-font-size));\n$messages-avatar-width: calc(32px + 4 * var(--default-grid-baseline));\n$messages-info-width: calc(8ch + var(--clickable-area-small, 24px) + 4 * var(--default-grid-baseline));\n$messages-list-max-width: calc($messages-avatar-width + $messages-text-max-width + 2 * var(--default-grid-baseline) + $messages-info-width);\n$messages-input-max-width: calc($messages-list-max-width - 100px);\n\n// background color of call container\n$color-call-background: rgba(34, 34, 34, 0.8);\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
